.comparison_value {
  max-width: 219px;
  padding: 15px 10px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  border-bottom: 1px solid var(--black);
}

@media (max-width: 860px) {
  .comparison_value {
    width: 100%;
    padding: 15px 10px;
    border-bottom: 1px solid var(--black);
  }
}
